body {
  background-color: rgb(236, 236, 236);
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.main {
  margin: 20px 40px;
  background: #ffffff;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  border-radius: 20px;

}
.main_heading {
  border-radius: 20px 20px 0 0;
  position: relative;
  top: 0;
  background-color: #c7c7c7;
  background: linear-gradient(#c7c7c7 0%, #8f8e8e 100%);
}
.main_heading_name {
  text-align: center;
  padding: 10px;
  font-size: 30px;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  margin-bottom: 0;;
}

.preloader{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content:center;
          justify-content:center;
  height: 100vh;
}
.preloader_img{
  margin:auto
}

.cards_container{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.content_heading{
    position: relative;
  
    background: linear-gradient(#B1B1B1 0%, #9c9c9c 100%);
    margin:0;
    height: 30px;
}
.content_heading_button{cursor: pointer;height: 20px; margin:5px; position: absolute;left:0;top:0;}
.content_heading_name{
    margin:auto;
    font-size: 20px;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    text-align: center;
    padding-top:5px;
}
.card{
    position:relative;  
    width:200px;
    min-height: 200px;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;  
    -webkit-align-items: center;  
            align-items: center;
    margin:20px;
    background-color: #FFC;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.35);
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    cursor: pointer;
    transition: .15s linear;
}
.card:hover:nth-child(n){
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
 .card:nth-child(even){  
    -webkit-transform:rotate(4deg);  
            transform:rotate(4deg);  
    position:relative;  
    top:5px;  
    background-color: #FFC;
  }  
  .card:nth-child(3n){  
    -webkit-transform:rotate(-3deg);  
            transform:rotate(-3deg);
    position:relative;  
    top:-5px;  
    background-color: #FFC;
  }  
  .card:nth-child(5n){    
    -webkit-transform:rotate(5deg);    
            transform:rotate(5deg);  
    position:relative;  
    top:-10px;  
    background-color: #FFC;
  }

.card__author-name{
    margin:auto;
}

.card p{
    display:block;
    text-align: center;
    cursor:default;
}
.card__title{
    margin:5px;
}
.card__img{ 
    height: 200px;
    width:200px;
    background-size: cover;
}

.card__length{
    position: absolute;
    top:0;
    right: 0;
    margin:10px;
    color: black;
    font-size: 1.5em;
}

.card__img-active{
    height: 200px;
    width:200px;
    background-size: cover;
    -webkit-filter: opacity(70%);
            filter: opacity(70%);
    transition:0.5s;}
.card__img-active:hover{-webkit-filter: opacity(100%);filter: opacity(100%);}
.cards-container{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.carusel_body{
    position: fixed;
    height: 100vh;
    width: 100vw;
    margin:auto;
    background-color: rgba(0, 0, 0, 0.411);
    top:0;
    left:0;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.carusel_element{
    object-fit: cover;
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    max-height: 85%;
    max-width: 85%;
    cursor: pointer;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.35);
}
.carusel_title{
    background-color: black;
    color:blanchedalmond;
    position:fixed;
    bottom: 0;
    right: 0;
    width: 100vw;
    text-align: center;
    margin-bottom: 0;
    z-index: 1000;
    font-size:20pt;
}
.next_button,.previous_button{
    height: 100vh;
    position: absolute;
    top:0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(0, 0, 0, 0.671);
    cursor: pointer;
 -webkit-user-select: none;
  -ms-user-select: none;
   user-select: none;
}

.previous_button{
    left:0;
}

.next_button img, .previous_button img{
height: 20px;
margin: 15px;
}

.previous_button img{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
